#plan-form {
  @include flex-col;

  .label {
    color: $orange;

    &--large {
      font-size: 2rem;
      font-weight: 600;
    }
  }
}

.plan-nav-buttons {
  @include flex-row(flex-end, center);
  margin-left: auto;

  & > * {
    margin-left: 1rem;
  }
}

.attendees,
.guests {
  &__col {
    @include flex-col;
  }
}
